<template>
  <div class="contentHtml">
  	  <ul>
  	  	<template v-for="listData in listDatas">
	  		<li class="mb10 initBox lineHeight" v-on:click="hrefTo('hrefTo','/index/answer/answerDetail/'+listData.id)">
	  			<div class="initTitle">{{listData.title}}</div>
  				<div class="color6 font14 ">{{listData.ask_content}}</div>
	  			<div class="line1"></div>
	  			<div class="color9 flex">
	  				<span class="width33">:{{listData.ancount}}</span>
	  				<span class="width33 text-c"></span>
	  				<span class="width33 text-r"></span>
	  			</div>
	  		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>
<script>
$("title").text("");
export default {
  name: 'contentHtml',
  data () {
    return {
    	typeIndex: 0,
    	images: [
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg'
    ],
    	listDatas:[],
    	input:'',
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //
	  var windowH = $(window).height(); //
	  var height = $(document).height();  //
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var start = _this.listDatas.length;
    		var per_page = 10;
    		var datas = {
    			"is_hot":-1,
    			"pageSize":per_page,
    			"pageIndex":0,
    			"title":'',
    			"status":start,
    		}
	  	$.get(_this.apiUrl+'/e/wxapi/ask_list.php',datas,function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
			if(res.length < per_page){
				$(".lodingDiv").text("").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		},'json')
  	},
	hrefTo(type,val){
		//ios
		if(this.utilIsLocalhost()==false){
			if(type=="hrefTo"){
				var content = {"type":"hrefTo","href":val}
			}else{
				var content = {"type":"href","id":val}
			}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		if(type=="hrefTo"){
	  			this.$router.push(val)
	  		}else{
	  			this.$router.push('/index/bidding/biddingDetail/'+val)
	  		}
  		}
	},
	typeIndexs(index){
		this.typeIndex = index;
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.typebox{text-align: center;line-height:24px;padding: 20px 0; font-size: 16px;background: #fff;}
	.typebox span{width: 49%;display: inline-block;}
	.typebox span:first-child{border-right: 1px solid #CCCCCC;}
	.iconType{text-align: center;font-size:12px;background: #fff;margin-bottom:10px;overflow: hidden;}
	.iconType li{width: 16.66%;padding:15px 0;float: left;}
	.iconType li img{width:3rem;height: 3rem;}
	.hideDiv{display: none;}
	.line1{margin: 5px 0 ;border-bottom: 1px dashed #ebebeb;}
	.initTitle{margin-bottom: 5px;}
</style>



